/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Reset perso */
a,
del,
ins {
  text-decoration: none;
}
a {
  color: inherit;
}
label,
button {
  cursor: pointer;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

#root {
  background-color: #fffafa;
  max-width: 990px;
  margin: auto;
  padding-bottom: 75px;
}

header {
  background-color: #dcdcdc;
  color: black;
  font-weight: bold;
  font-size: 2rem;

  padding: 2rem;
  text-align: center;
  display: flex;
  flex-flow: column nowrap;
}

h1 {
  margin: auto;
}

main {
  background-color: #ccccff;
  max-width: 757px;
  margin: 75px auto;
  overflow: hidden;
}

footer {
  background-color: #effeef;
  max-width: 757px;
  margin: 75px auto;
  display: flex;
  flex-flow: column nowrap;
}

footer > * {
  margin: auto;
}
